fff;\n$color-title: #282830;\n$color-text: #6A6B70;\n$color-input-text: #555555;\n$color-border: #acb0b7;\n$color-border-focus: #4c4e51;\n$color-border-hover: #86898e;\n$color-danger: #e74c3c;\n$color-success: #1abc9c;\n//$color-overlay: #460309;\n$color-overlay: #14469E;\n\n* {\n    margin: 0;\n    padding: 0;\n}\n\nhtml {\n    box-sizing: border-box;\n}\n\n*,\n*:before,\n*:after {\n    margin: 0;\n    padding: 0;\n    box-sizing: inherit;\n}\n\n/* BODY */\n\nbody {\n    position: relative;\n    color: $color-text;\n    font: 16px/26px \"Roboto\", sans-serif !important;\n    text-align: center;\n    height: 100%;\n    overflow: hidden;\n    //background-image: url('../../../images/login-bg.jpg');\n    background-repeat: no-repeat;\n    background-size: cover;\n    background-position: center top;\n}\n\n.fill {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n}\n\n.login {\n    margin: 0;\n    width: 100%;\n    height: 100%;\n    min-height: 100vh;\n    display: flex;\n\n    .content {\n        display: flex;\n        position: relative;\n        width: calc(100% - 400px);\n        background-size: cover;\n        background-position: center top;\n        height: 100vh;\n\n        &:before {\n            position: absolute;\n            background-color: rgba($color-overlay, .3);\n            height: 100%;\n            width: 100%;\n            display: block;\n            content: \"\";\n        }\n\n        .title {\n            position: absolute;\n            left: 80px;\n            display: flex;\n            flex-direction: column;\n            justify-content: center;\n            align-items: flex-start;\n            width: 500px;\n            height: 500px;\n            z-index: 10;\n            text-align: left;\n\n            h2 {\n                font-family: 'Roboto', sans-serif;\n                max-width: 500px;\n                font-size: 28px;\n                margin-bottom: 10px;\n                line-height: 32p